import { WordpressService } from './../../providers/word-press/word-press';
import { Component, OnInit } from '@angular/core';
import { LoadingController } from '@ionic/angular';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss']
})
export class HomePage implements OnInit {

  posts = [];
  page = 1;
  count = null;
  categories = [];
  categoryID = 0;
  postsPerCat = [];
  isFirst = false;
  first: boolean;
  postsPerCat1  = [];
  postsPerCat2  = [];
  postsPerCat3  = [];

  sliderConfig1 = {
    slidesPerView: 1
  };

  sliderConfig2 = {
    slidesPerView: 2
  };

  sliderConfig3 = {
    slidesPerView: 3
  };

  constructor(private wp: WordpressService, private loadingCtrl: LoadingController) {}

  ngOnInit() {
    this.loadPosts();
    this.getPostByCategories1(653);
    this.getPostByCategories2(657);
    this.getPostByCategories3(658);
  }

  async loadPosts() {
    let loading = await this.loadingCtrl.create({
      message: 'Loading Data...',
      translucent: true,
      cssClass: 'custom-loading'
    });
    await loading.present();

    this.wp.getPosts(this.page).subscribe(res => {
      this.count = this.wp.totalPosts;
      this.posts = res;
      loading.dismiss();
    });
  }

  async getPostByCategories1(id){
    let loading = await this.loadingCtrl.create({
      message: 'Loading Data...',
      translucent: true,
      cssClass: 'custom-loading'
    });
    await loading.present();

    this.first = false;
    this.isFirst = false;
    this.wp.getPostsByCategory(id).subscribe(res => {
      this.postsPerCat1 = res;
      this.count = this.wp.totalPosts;
      //console.log(this.postsPerCat);
      //console.log(this.categories);
      loading.dismiss();

    });
  }

  async getPostByCategories2(id){
    let loading = await this.loadingCtrl.create({
      message: 'Loading Data...',
      translucent: true,
      cssClass: 'custom-loading'
    });
    await loading.present();

    this.first = false;
    this.isFirst = false;
    this.wp.getPostsByCategory(id).subscribe(res => {
      this.postsPerCat2 = res;
      this.count = this.wp.totalPosts;
      //console.log(this.postsPerCat);
      //console.log(this.categories);
      loading.dismiss();

    });
  }

  async getPostByCategories3(id){
    let loading = await this.loadingCtrl.create({
      message: 'Loading Data...',
      translucent: true,
      cssClass: 'custom-loading'
    });
    await loading.present();

    this.first = false;
    this.isFirst = false;
    this.wp.getPostsByCategory(id).subscribe(res => {
      this.postsPerCat3 = res;
      this.count = this.wp.totalPosts;
      //console.log(this.postsPerCat);
      //console.log(this.categories);
      loading.dismiss();

    });
  }

  loadMore(event) {
    this.page++;

    this.wp.getPosts(this.page).subscribe(res => {
      this.posts = [...this.posts, ...res];
      event.target.complete();

      if (this.page == this.wp.pages) {
        event.target.disabled = true;
      }
    });
  }

}
